  export default {
    data() {
      return {
        begin: false,//开始录制
        pause: false,//暂停推流
        currentWebview: null,
        pusher: null,
        livepushurl: '',
        whitenessLevel: 2,
        isBeautiful: 0,
        array: [
          {
            level: 0
          },
          {
            level: 1
          },
          {
            level: 2
          },
          {
            level: 3
          },
          {
            level: 4
          },
          {
            level: 5
          }
        ]
      }
    },

    onShow() {
      uni.getNetworkType({
        success: function (res) {
          if (res.networkType != 'wifi') {
            uni.showModal({
              title: '温馨提示',
              content: '当前非Wifi网络，请注意您的流量是否够用',
              success: (res) => {

              }
            })
          }
        }
      })
      uni.onNetworkStatusChange(function (res) {
        if (res.networkType != '4g' && res.networkType != 'wifi') {
          uni.showModal({
            title: '温馨提示',
            content: '当前网络质量差，请切换为4G网络或Wifi网络',
            success: (res) => {
            }
          })
        }
      })
    },
    onLoad() {
      this.livepushurl = uni.getStorageSync('pullurl')
      console.log(uni.getStorageSync('pullurl'))
      this.getwebview()
    },
    methods: {
      bindPickerChange (e) {
        this.pusher.setStyles({
          whiteness: parseInt(e.target.value)
        })
        this.$toast(`美白等级${e.target.value}`)
      },
      back () {
        this.$Dialog.confirm({
          message: '确定退出直播'
        }).then(() => {
          this.pusher.stop()
          this.getback()
        })
      },

      getwebview() {
        var pages = getCurrentPages();
        var page = pages[pages.length - 1];






      },

			/**
			 * 创建LivePusher对象 即推流对象
			 */
      plusReady() {
        this.pusher = new plus.video.LivePusher('pusher', {
          url: '',
          top: '0',
          left: '0px',
          width: '100%',
					height: uni.getSystemInfoSync().windowHeight - uni.upx2px(140) + 'px',
          position: 'absolute',
          beauty: 0,
          whiteness: 0,
          aspect: '9:16',
        })
        this.currentWebview.append(this.pusher)
        this.pusher.addEventListener('statechange', (e) => {
          console.log('statechange: ' + JSON.stringify(e))
        }, false);
      },

      beautiful() {
        if (this.isBeautiful) {
          this.pusher.setStyles({
            beauty: 0
          })
          this.isBeautiful = false
          this.$toast('关闭美颜')
        } else {
          this.pusher.setStyles({
            beauty: 1
          })
          this.isBeautiful = true
          this.$toast('开启美颜')
        }
      },

      startPusher() {
        this.beginlivepush()
      },
      beginlivepush() {
        if (this.begin == false) {//未开启推流
          this.begin = true;//显示录制动画
          // 设置推流服务器  ***此处需要通过ajax向后端获取
          this.pusher.setStyles({
            url: this.livepushurl //推流地址********************************* 此处设置推流地址
          });
          this.pusher.start();//推流开启
          uni.showToast({
            title: '开始录制',
            icon: 'none',
            duration: 2000,
          });
        } else {
          if (this.pause == true) {//暂停推流状态
            this.begin = true;//显示录制动画
            this.pause = false;//推流开关置为默认状态
            this.pusher.resume();//恢复推流
            uni.showToast({
              title: '开始录制',
              icon: 'none',
              duration: 2000,
            });
          } else {
            this.begin = false;//关闭录制动画
            this.pause = true;//推流暂停
            this.pusher.pause();;//暂停推流
            uni.showToast({
              title: '暂停录制',
              icon: 'none',
              duration: 2000,
            })
          }
        }
      },
			/**
			 * 切换摄像头
			 */
      switchCamera() {
        this.pusher.switchCamera();
      }
    }
  }
